import React, { useEffect, useState } from 'react'
import { Dimensions, Platform, View } from 'react-native'
import useIsMobile from '../hooks/useIsMobile'

type ViewProps = {windowType:'landscape'|'portrait', children:React.ReactNode}

export default (props:ViewProps)=>{
    const isMobile = useIsMobile()
    const [height, setHeight] = useState(window.innerHeight + 1)
    useEffect(()=>{
        if(isMobile && Platform.OS == 'web'){
            const onChange = (e:any)=>{
                setHeight(window.innerHeight + 1)
            }
            const onChangeOrientation = (e:any)=>{
                setHeight(window.innerWidth + 1)
            }
            window.addEventListener("touchmove", onChange)
            Dimensions.addEventListener("change",onChangeOrientation)
            // if (screen?.orientation)
            //     screen.orientation.addEventListener("change",onChangeOrientation)
            // else
            //     window.addEventListener("orientationchange", onChangeOrientation)
            return () => {
                window.removeEventListener("touchmove", onChange)
                Dimensions.removeEventListener("change", onChangeOrientation)
                // if (screen?.orientation)
                //     screen.orientation.removeEventListener("change",onChangeOrientation)
                // else
                //     window.removeEventListener("orientationchange", onChangeOrientation)
            }
        }
    },[ isMobile])
    return <View style={{height:isMobile&&Platform.OS == 'web'?height:'100%'}}>
        {props.children}
    </View>

}
  